import Home from "../portfolio-container/Home/Home";
import AboutMe from "../portfolio-container/AboutMe/AboutMe";
import Resume from "../portfolio-container/Resume/Resume";
import Testimonial from "../portfolio-container/Testimonial/Testimonial";
import ContactMe from "../portfolio-container/ContactMe/ContactMe";
import Projects from "../portfolio-container/Project/Projects";

export const TOTAL_SCREENS = [
  {
    screen_name: "Home",
    component: Home
  },
  {
    screen_name: "AboutMe",
    component: AboutMe
  },
  {
    screen_name: "Resume",
    component: Resume
  },
  {
    screen_name: "Testimonial",
    component: Testimonial
  },
  {
    screen_name: "ContactMe",
    component: ContactMe
  },
  {
    screen_name: "Projects",
    component: Projects
  },
  {
    screen_name: "",
    component: ""
  }
];

export const GET_SCREEN_INDEX = (screen_name) => {
  if (!screen_name) return -1;

  for (let i = 0; i < TOTAL_SCREENS.length; i++) {
    if (TOTAL_SCREENS[i].screen_name === screen_name) return i;
  }

  return -1;
};
